import * as React from 'react';
import Paper from '@mui/material/Paper';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell, { tableCellClasses } from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TablePagination from '@mui/material/TablePagination';
import TableRow from '@mui/material/TableRow';
import data from '../data/messages.json';
import { styled } from '@mui/material/styles';
import axios from 'axios';
import { useEffect } from 'react';

interface Column {
  id: 'type' | 'lat' | 'lon' | 'location' | 'message' | 'level';
  label: string;
  minWidth?: number;
  align?: 'right';
  format?: (value: number) => string;
}

const StyledTableCell = styled(TableCell)(({ theme }) => ({
    [`&.${tableCellClasses.head}`]: {
      backgroundColor: theme.palette.secondary.dark,
      color: theme.palette.primary.main,
    },
    [`&.${tableCellClasses.body}`]: {
      fontSize: 14,
    },
  }));

  const StyledTableRow = styled(TableRow)(({ theme }) => ({
    '&:nth-of-type(odd)': {
      backgroundColor: theme.palette.action.hover,
    },
    // hide last border
    '&:last-child td, &:last-child th': {
      border: 0,
    },
  }));

const columns: readonly Column[] = [
  { id: 'type', label: 'Type', minWidth: 10 },
  { id: 'lat', label: 'Latitude', minWidth: 18, format: (value: number) => value.toLocaleString('en-US')},
  { id: 'lon', label: 'Longitude', minWidth: 18, format: (value: number) => value.toLocaleString('en-US')},
  {
    id: 'location',
    label: 'Location',
    minWidth: 20,
    align: 'right',
  },
  {
    id: 'message',
    label: 'Message',
    minWidth: 40,
    align: 'right',
  },
  {
    id: 'level',
    label: 'Level',
    minWidth: 3,
    align: 'right',
  },
];

interface Data {
    type: string,
    lat: number,
    lon: number,
    location: string,
    message: string,
    level: string,
}

function createData(
    type: string,
    lat: number,
    lon: number,
    location: string,
    message: string,
    level: string,
): Data {
  return { type, lat, lon, location, message, level };
}


