export default function EmergenciesTable() {
  const [page, setPage] = React.useState(0);
  const [rowsPerPage, setRowsPerPage] = React.useState(25);
  const [rows, setRows] : any = React.useState([]);

  const createRows = async () => {
    try {
      const response = await axios.get('http://org.emergencies.ml/api/show-emergencies',
      {
        headers: {
          'Content-Type': 'application/json',
        }
      });
      // console.log(response);
      let rows: Data[] = [];
      let emergencies =  response.data.emergencies;

      for (let i = 0; i < emergencies.length; i++) {
          rows.push(createData(emergencies[i].type, emergencies[i].lat, emergencies[i].lon, emergencies[i].location, emergencies[i].message, emergencies[i].level));
      }

      return rows;
    }
    catch (error) {
      console.log(error);
      return error
    }
  }

  useEffect( () => {

    createRows().then(
      (result : any) => {
        setRows([...result]);
      }
    );
  },[])

  const handleChangePage = (event: unknown, newPage: number) => {
    setPage(newPage);
    // console.log(page);
    // console.log(newPage);
  };

  const handleChangeRowsPerPage = (event: React.ChangeEvent<HTMLInputElement>) => {
    setRowsPerPage(+event.target.value);
    setPage(0);
  };

  return (
    <Paper sx={{ width: '100%', overflow: 'hidden'}}>
      <TableContainer sx={{ maxHeight: 600 }}>
        <Table stickyHeader aria-label="sticky table">
          <TableHead>
            <StyledTableRow>
              {columns.map((column) => (
                <StyledTableCell
                  key={column.id}
                  align={column.align}
                  style={{ minWidth: column.minWidth }}
                >
                  {column.label}
                </StyledTableCell>
              ))}
            </StyledTableRow>
          </TableHead>
          <TableBody>
            {rows
              .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
              .map((row : Data[], index : number) => {
                return (
                  <TableRow hover role="checkbox" tabIndex={-1} key={index}>
                    {columns.map((column : any) => {
                      const value = row[column.id];
                      return (
                        <TableCell key={column.id} align={column.align}>
                          {column.format && typeof value === 'number'
                            ? column.format(value)
                            : value}
                        </TableCell>
                      );
                    })}
                  </TableRow>
                );
              })}
          </TableBody>
        </Table>
      </TableContainer>
      <TablePagination
        rowsPerPageOptions={[10, 25, 100]}
        component="div"
        count={rows.length}
        rowsPerPage={rowsPerPage}
        page={page}
        onPageChange={handleChangePage}
        onRowsPerPageChange={handleChangeRowsPerPage}
      />
    </Paper>
  );
}